import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import { BsSearch } from "react-icons/bs";
import { UseState } from 'react';
//import '../Css/NavBar.css'
import { filtradoNombre } from '../Redux/actions';

function paginaPrincipal({filtradoNombre}){
    const [nombre, setNombre] = UseState('');

    function cambiaNombre(e){
        setNombre(e.target.value);
    }

    function buscarNombre(nombre){
        filtradoNombre(nombre);
    }

    return (
        <nav className="nav">
            <label className="label">Henry Dogs</label>
            <span className="subNav">
                <span><input type="text" value={nombre} onChange={cambiaNombre} placeholder="Nombre" className="styleM"/><button onClick={() => buscarNombre(nombre)} className="button"><BsSearch/></button></span>
                <Link to="/principal/form">
                    <button className="butAgregar">Agregar raza de perro</button>
                </Link>
                <Link to="/">
                    <button className="butPaginaPrincipal">Página principal</button>
                </Link>
                
            </span>
        </nav>
    )
}

function mapDispatchToProps(dispatch){
    return {
        filtradoNombre: function(nombre){
            return dispatch(filtradoNombre(nombre))
        }
    }
}

export default connect(null, mapDispatchToProps)(paginaPrincipal);